<template>
  <section class="section-three" id="sectionThree">
    <div class="g-container">
      <div class="section-three-box">
        <h2>Section 3</h2>
        <div class="slider-wrapper">
          <div class="images">
            <span class="slider-count">{{
              `Image: ${this.active + 1} of ${this.slider.length}`
            }}</span>
            <div>
              <img
                v-for="(img, index) in slider"
                :key="img.id"
                v-show="index === active"
                class="displayed-img"
                :src="img.url"
                :alt="img.title"
                draggable="false"
              />
            </div>
            <div class="left-arrow" @click="change(active - 1)">
              <i class="bx bx-chevron-left"></i>
            </div>
            <div class="right-arrow" @click="change(active + 1)">
              <i class="bx bx-chevron-right"></i>
            </div>
          </div>
          <div class="thumbnails">
            <ul>
              <li
                v-for="(img, index) in slider"
                :class="{ active: index === active }"
                :key="img.id"
                @click="change(index)"
              >
                <img
                  :src="img.thumbnailUrl"
                  :alt="img.thumbnailUrl"
                  draggable="false"
                />
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import axios from "axios";
export default {
  name: "SectionThree",
  data() {
    return {
      active: 0,
      slider: [],
    };
  },
  methods: {
    change(index) {
      let total = this.slider.length;
      this.active = (index + total) % total;
    },
  },
  created() {
    axios
      .get(`https://jsonplaceholder.typicode.com/photos?albumId=1`)
      .then((response) => {
        this.slider = response.data;
      })
      .catch((err) => {
        console.log(err);
      });
    setInterval(() => {
      this.change(this.active + 1);
    }, 3000);
  },
  mounted() {
    let thumbnails = document.querySelector(".section-three .thumbnails");

    thumbnails.addEventListener("wheel", (evt) => {
      evt.preventDefault();
      thumbnails.scrollLeft += evt.deltaY;
    });
  },
};
</script>

<style lang="scss" scoped>
.section-three {
  background-color: #f8f8f8;
  min-height: calc(100vh - 76px);
  .section-three-box {
    padding: 60px 0;
    border-top: 2px solid #e8e8e8;
    h2 {
      margin-bottom: 20px;
    }
    .slider-wrapper {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .images {
        width: 600px;
        height: 400px;
        overflow: hidden;
        position: relative;
        border-radius: 10px;
        .slider-count {
          position: absolute;
          bottom: 10px;
          left: 50%;
          transform: translateX(-50%);
          color: #fff;
          font-weight: 700;
        }
        div {
          width: 100%;
          height: 100%;
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }
        .left-arrow,
        .right-arrow {
          position: absolute;
          z-index: 10;
          width: 40px;
          cursor: pointer;
          background-color: rgba(255, 255, 255, 0.5);
          top: 0;
          bottom: 0;
          display: flex;
          justify-content: center;
          align-items: center;
          opacity: 0;
          transition: all 0.3s ease-in-out;
          i {
            font-size: 2rem;
            color: #fff;
          }
        }
        .left-arrow {
          left: 0;
        }
        .right-arrow {
          right: 0;
        }
        &:hover {
          .left-arrow,
          .right-arrow {
            opacity: 1;
          }
        }
      }
      .thumbnails {
        margin-top: 10px;
        width: 600px;
        height: 180px;
        overflow: hidden;
        overflow-x: scroll;
        scrollbar-width: thin;
        scrollbar-color: #00516d #9ae5ff;
        &::-webkit-scrollbar {
          height: 8px;
        }

        &::-webkit-scrollbar-track {
          background: #9ae5ff;
        }

        &::-webkit-scrollbar-thumb {
          background-color: #00516d;
          border: 3px solid #00516d;
        }
        ul {
          display: flex;
          align-items: center;
          padding-left: 5px;
          gap: 10px;
          position: relative;

          li {
            min-width: 190px;
            height: 150px;
            border-radius: 10px;
            overflow: hidden;
            cursor: pointer;
            img {
              height: 100%;
              object-fit: cover;
            }
            &.active {
              border: 3px solid #f00;
            }
          }
        }
      }
    }
  }
  @media (max-width: 768px) {
    .section-three-box {
      .slider-wrapper {
        .images {
          width: 400px;
        }
        .thumbnails {
          width: 400px;
        }
      }
    }
  }
  @media (max-width: 460px) {
    .section-three-box {
      .slider-wrapper {
        .images {
          width: 100%;
          height: 300px;
        }
        .thumbnails {
          width: 220px;
          height: 115px;
          ul {
            li {
              min-width: 100px;
              height: 100px;
            }
          }
        }
      }
    }
  }
}
</style>
