<template>
  <header>
    <div class="top-header">
      <div class="g-container">
        <div class="header-content">
          <div>
            <h1>What it's like to travel to Hawaii right now</h1>
            <p>Free flights to remote workers</p>
          </div>
        </div>
      </div>
    </div>
    <nav>
      <button @click="openNav" :class="{ active: isActive }" class="navbtn">
        <span></span>
        <span></span>
        <span></span>
      </button>
      <ul :class="{ active: isActive }">
        <li><a href="#sectionOne" class="active"> Section 1</a></li>
        <li><a href="#sectionTwo">Section 2</a></li>
        <li><a href="#sectionThree">Section 3</a></li>
      </ul>
    </nav>
  </header>
</template>

<script>
export default {
  name: "Header",
  data() {
    return {
      isActive: false,
    };
  },
  methods: {
    openNav() {
      this.isActive = !this.isActive;
    },
  },
  mounted() {
    const nav = document.querySelector("nav");
    const navLinks = document.querySelectorAll("nav li a");
    const section = document.querySelectorAll("section");
    window.addEventListener("scroll", function () {
      let sticky = nav.offsetTop;
      if (window.pageYOffset > sticky) {
        nav.classList.add("sticky");
      } else {
        nav.classList.remove("sticky");
      }
      section.forEach((sec) => {
        let top = window.scrollY;
        let offset = sec.offsetTop - 300;
        let height = sec.offsetHeight;
        let id = sec.getAttribute("id");

        if (top >= offset && top < offset + height) {
          navLinks.forEach((links) => {
            links.classList.remove("active");
            let anchor = document.querySelector(`header nav a[href*="${id}"]`);
            anchor.classList.add("active");
          });
        }
      });
    });
  },
};
</script>

<style scoped lang="scss">
header {
  display: grid;
  .top-header {
    background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.7),
        rgba(109, 109, 109, 0.12)
      ),
      url("../assets/img/MainImage.png");
    background-size: cover;
    background-attachment: fixed;
    min-height: calc(100vh - 76px);
    .header-content {
      display: grid;
      grid-template-columns: 0.5fr;
      align-items: center;
      justify-content: start;
      min-height: calc(100vh - 76px);
      div {
        padding: 10px;
        color: #fff;
        h1 {
          font-size: 3rem;
          line-height: 1.5;
          font-weight: 400;
        }
        p {
          border-top: 1px solid #fff;
          display: inline-block;
          margin-top: 10px;
          padding-top: 10px;
        }
      }
    }
  }
  nav {
    background-color: #fff;
    .navbtn {
      display: none;
    }
    ul {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      li {
        a {
          display: flex;
          align-items: center;
          justify-content: center;
          padding: 24px 5px;
          cursor: pointer;
          border-bottom: 4px solid transparent;
          transition: all 0.3s ease-in-out;
          color: #000;
          text-decoration: none;
          &.active,
          &:hover {
            border-bottom: 4px solid #0a527b;
            color: #0a527b;
          }
        }
      }
    }
    &.sticky {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      z-index: 9999;
    }
  }
  @media (max-width: 768px) {
    .top-header {
      order: 2;
      min-height: calc(100vh - 48px);
      .header-content {
        grid-template-columns: 1fr;
        min-height: calc(100vh - 48px);
        div {
          h1 {
            font-size: 2rem;
          }
        }
      }
    }
    nav {
      order: 1;
      position: relative;
      display: flex;
      align-items: center;
      padding: 10px 20px;
      .navbtn {
        display: flex;
        background-color: transparent;
        border: 0;
        outline: none;
        flex-direction: column;
        justify-content: center;
        align-items: flex-end;
        cursor: pointer;
        margin-left: auto;
        min-height: 28px;
        min-width: 25px;
        span {
          display: block;
          background-color: #000;
          width: 24px;
          height: 3px;
          margin: 3px 0;
          border-radius: 10px;
          transition: all 0.3s ease-in-out;
          &:nth-child(2) {
            width: 18px;
            transition: all 0.2s ease-in-out;
          }
        }
        &.active {
          height: 27px;
          span {
            background-color: #0a527b;
            margin: -2px auto;
            border-radius: 10px;
            &:first-child {
              transform: rotate(-45deg);
            }
            &:nth-child(2) {
              display: none;
            }
            &:last-child {
              transform: rotate(45deg);
            }
          }
        }
      }
      ul {
        background-color: #fff;
        grid-template-columns: 1fr;
        position: absolute;
        top: 100%;
        left: 0;
        right: 0;
        height: 0;
        max-height: 0;
        overflow: hidden;
        top: 100%;
        border-top: 1px solid #ccc;
        transition: all 0.3s ease-in-out;
        overflow-x: hidden;
        li {
          a {
            border-left: 5px solid transparent;
            border-bottom: none;
            &.active,
            &:hover {
              border-left: 5px solid #0a527b;
              border-bottom: none;
            }
          }
        }
        &.active {
          height: 217px;
          max-height: calc(100vh - 48px);
        }
      }
    }
  }
}
</style>
